.form-roomsearch {
    background-color: #fff;
    padding: 30px;
    border: 1px solid rgba(0, 0, 0, 0.12);
    box-shadow: 0 0 25px rgba(0, 0, 0, 0.12);
    border-radius: 4px;
    max-width: 380px;
    box-sizing: border-box;
    &__title {
        margin-bottom: 38px;
        @extend %h1;
        margin-top: 9px;
        line-height: 132%;
    }
    &__dates {
        display: flex;
        justify-content: space-between;
        margin-bottom: 41px;
    }
    &__check-in, &__check-out {
        width: 47%;
    }
    &__submit {
        margin-top: 32px;
    }


}