}

.datepicker--nav-title {
  border-radius: 4px;
  padding: 0 8px;
  font-family: "Open Sans";
  font-size: 19px;
  color: #1F2041;
  font-weight: 700;
  margin-left: 6px;
}
.datepicker--buttons,
.datepicker--time {
  // border-top: 1px solid #efefef;
  padding: 4px;
  flex-direction: row-reverse;
}
.datepicker--nav-title i {
  font-style: normal;
  // color: #9c9c9c;
  margin-left: 5px;
}
.datepicker--nav-title.-disabled- {
  cursor: default;
  background: 0 0;
}
.datepicker--buttons {
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  justify-content: space-between;
  margin-top: 2px;
  margin-bottom: 7px;
}

.datepicker--button {
  @extend %h3;
  letter-spacing: -.01em;
  // color: $dark100;
  opacity: 0.5;
  max-width: 100px;

  // color: #4eb5e6;
  cursor: pointer;
  border-radius: 4px;
  -webkit-flex: 1;
  -ms-flex: 1;
  flex: 1;
  display: -webkit-inline-flex;
  display: -ms-inline-flexbox;
  display: inline-flex;
  -webkit-justify-content: center;
  -ms-flex-pack: center;
  justify-content: center;
  -webkit-align-items: center;
  -ms-flex-align: center;
  align-items: center;
  height: 32px;
  &:nth-child(2) {
    color: $dark100;
    opacity: 0.5;
  }
  &:nth-child(1) {
    color: $purple;
    opacity: 1;
    margin-right: 8px;
  }
