@import "../../../node_modules/slick-carousel/slick/slick.css";
@import "../../../node_modules/slick-carousel/slick/slick-theme.css";

.slick-slider {
    margin: auto;
}
.slick-slide {
    width: 270px;
    object-fit: cover;
}
.slick-track {
    height: 151.38px;
}
.slick-prev, .slick-next {
    z-index: 1;
    width: 42px;
    height: 100%;
}
.slick-prev {
    left: 0;
    background: linear-gradient(90deg, rgba(0, 0, 0, 0.5) 0%, rgba(0, 0, 0, 0) 99.99%, rgba(255, 255, 255, 0) 100%);
}
.slick-next {
    right: 0;
    background: linear-gradient(-90deg, rgba(0, 0, 0, 0.5) 0%, rgba(0, 0, 0, 0) 99.99%, rgba(255, 255, 255, 0) 100%);
}
.slick-prev:before {
    content: 'expand_more';
    transform: rotate(90deg);
}
.slick-next:before {
    content: 'expand_more';
    transform: rotate(-90deg);
}
.slick-prev:before, .slick-next:before {
    font-family: 'Conv_MaterialIcons-Regular';
    font-weight: normal;
    font-style: normal;
    font-size: 24px;
    display: inline-block;
    line-height: 1;
    text-transform: none;
    letter-spacing: normal;
    word-wrap: normal;
    white-space: nowrap;
    direction: ltr;
    background-color: #FFF;
    opacity: 1;

    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  
    /* Support for all WebKit browsers. */
    -webkit-font-smoothing: antialiased;
    /* Support for Safari and Chrome. */
    text-rendering: optimizeLegibility;
  
    /* Support for Firefox. */
    -moz-osx-font-smoothing: grayscale;
  
    /* Support for IE. */
    font-feature-settings: 'liga';
}

.slick-list {
    border-radius: 4px 4px 0 0;
}

.slick-dots li {
    width: 1px;
}

.slick-dots li button:before {
    position: absolute;
    top: 0;
    left: 0;
    width: 7.5px;
    height: 7.5px;
    content: '';
    text-align: center;
    opacity: 1;
    color: black;
    border-radius: 50%;
    border: 1px solid white;
    box-sizing: border-box;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}
.slick-active button:before {
    background-color: white;
    opacity: 1;
    box-sizing: border-box;
}

.slick-dots {
    bottom: 2px;
    padding-left: 36;
    z-index: 1;
}
.slick-dots li.slick-active button:before {
    opacity: 1;
}
.slick-dotted.slick-slider {
    margin-bottom: 0px;
}

.room {
    max-width: 270px;
    background-color: #fff;
    box-shadow: 0px 10px 20px rgba(31, 32, 65, 0.05);
    border-radius: 0 0 4px 4px;
    &__card {
        // width: 270px;
        height: 151.38px;
        width: 100%;
    }
    &__room-description {
        display: flex;
        justify-content: space-between;
        align-items: baseline;
        margin: 21px 17px 10px 19px;
    }
    &__room-number {
        @extend %h1;
        font-size: 19px;
        display: inline-block;
        & span {
            font-size: 14px;
        }
    }
    &__room-type {
        @extend %h3;
        color: $purple;
        display: inline-block;
        margin-left: 5px;
    }
    &__price {
        @extend %h3;
        font-size: 14px;
        color: $dark50;
        & span {
            @extend %font-body;
            font-size: 12px;
            color: $dark50;
            text-transform: none;
        }
    }
    &__review {
        @extend %h3;
        font-size: 14px;
        color: $dark50;
        margin-bottom: 2px;
        & span {
            @extend %font-body;
            font-size: 14px;
            color: $dark50;
            text-transform: none;
        }
    }
    &__line {
        height: 1px;
        background: rgba(31, 32, 65, 0.1);
        margin: 0 20px 8px 20px;
    }
    &__rate {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin: 0 20px 0 20px;
        padding-bottom: 14px;
    }
}